  CONTAINS = 'CONTAINS',
  REGULAR_EXPRESSION = 'REGULAR_EXPRESSION',
  EQUAL = 'EQUAL',
  LOWER = 'LOWER',
  GREATER = 'GREATER',
  UNDEFINED = 'UNDEFINED',
